div.bodyStyles {
	padding: 0;
	margin: 0;
	overflow-y: hidden;
}
.transaction__box {
	width: 100%;
	display: flex;
	flex-direction: column;
}
.scrollableDiv {
	border-radius: var(--border-radius);
	overflow-y: auto;
}
.transactions__search {
	width: 100%;
	position: relative;
}

.transactions__search__icon {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	left: 17px;
	z-index: 1;
}

.transactions__search__input {
	padding-left: 44px !important;
}

.transactions__filter {
	border-radius: 4px;
	cursor: pointer;
	min-width: 48px;
	height: 48px;
	margin-left: 12px;
	background: var(--bg-white);
}

.transactions__filter svg path {
	fill: var(--chakra-colors-chakra-body-text);
}

.transactions__list_month {
	padding: 0 2px;
}

.transactions__month_header {
	margin-bottom: 16px;
	margin-top: 24px;
}

.transactions__list_item {
	cursor: pointer;
	display: flex;
	flex-direction: row;
	align-items: center;
	padding: 20px 15px;
	border-top: 1px solid var(--border-light);
	border-radius: 4px;
}

.tag {
	border-radius: 4px;
	font-size: 12px;
	height: 64px;
	line-height: 24px;
	text-align: center;
	margin-right: 24px;
	width: 64px;
	height: 24px;
	background-color: rgb(16 147 245 / 10%);
	color: #1093F5;
	white-space: nowrap;
}

.tag__intrabank {
	background-color: #5C33E61A;
	color: #5C33E6;
}
.tag__card {
	background-color: #1AAEC71A;
	color: #1AAEC7;
}

.transaction__description {
	margin-right: auto;
	padding-right: 5px;
	flex: 1;
}

.transaction__received {
	color: #2EC193;
	font-weight: 500;
}
.transaction__sent {
	color: #FF4A4A;
	font-weight: 500;
}